
import Vue from 'vue'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

Vue.use(LoadingPlugin)
export default {
  directives: {
    TransferDom
  },
  components: {
    XButton,    
    PopupPicker,
    Confirm,
    TransferDom,
    Toast,
    Grid,
    Group,
    GridItem,
    Datetime
  },
  data () {
      return {
        show: false,
        showPositionValue: false,
        tips:"",
        items:[],
        interviewDate:new Date(),
        jobName:'',
        idx:0,//当前选择索引,
        moreDate:'更多',
        headerDate:[],
        headerIdx:0,
        paramsId:0,
        headerHasData:[],
        hasDataIndex:-1,
        selectDay:''
      }
  },
  created() {
    // 显示
    this.$vux.loading.show({
    text: '加载中...'
    })
    if(this.$store.state.applies.length==0){
    this.$http({
            method:'POST',
            url:this.$store.state.domain+'api/XiechengJobAPI/GetApplies',
            body:{},
            headers: {"Authorization": this.$store.state.jwt.token}
            })
        .then((d) => {
                if(d.data.success){
                  this.$store.state.applies=d.data.content.items;
                  this.getJobName();
                }else{
                  this.showPosition(d.data.message);
                }
              }, (d) => {
                this.showPosition('网络繁忙！请稍候重再试');
        });
    }else{
      this.getJobName();
    }
  },
  mounted() {
      var dateee = this.interviewDate.toJSON().replace(/T/g,' ').replace(/\.[\d]{3}Z/,'').replace(/-/g,'\/');
      var _obj=new Date(dateee)// +8*3600*1000
    //this.getDescribeTime(this.addDate(_obj,this.hasDataIndex+1))// 获取预约时间段
    this.checkTimes(_obj,0);//给有数据的日期加绿色
    this.createDate()//创建头部日期选项
    
  },
  filters:{
    formatTime(value){
      var dateee=value.replace(/T/g,' ').replace(/-/g,'\/');
      //console.log(dateee);
      //var dateee = new Date(value).toJSON(); 
      //var _obj=new Date(dateee).toISOString().replace(/\.[\d]{3}Z/,'')// +8*3600*1000
      var _obj=new Date(dateee)
      var _h=_obj.getHours();
      if(_h.toString().length==1) _h="0"+_h
      var _m=_obj.getMinutes();
      if(_m.toString().length==1) _m="0"+_m
      return _h+" : "+_m;
    }
  },
  methods:{
    change(value){
      if(value){
        this.moreDate=''
        this.headerIdx=4
        this.getDescribeTime(value)
      }
      //console.log(value)
    },
    getJobName(){
      //取职位名称
      for(var i=0;i<this.$store.state.applies.length;i++){
        if(parseInt(this.$store.state.applies[i].id)==parseInt(this.$route.params.id)){
          this.jobName=this.$store.state.applies[i].jobName
          break
        }
      }
    },
    onCancel () {
      //console.log('on cancel')
    },
    onConfirm () {
      this.showPositionValue = true
        this.$http({
          method:'POST',
          url:this.$store.state.domain+'api/XiechengJobAPI/SetApplyInterviewTime',
          body:{"applyId": this.$route.params.id,"interviewTime": this.items[this.idx].time},
          headers: {"Authorization": this.$store.state.jwt.token}
          })
      .then((d) => {
              if(d.data.success){
                this.showPosition('预约成功');
                this.items[this.idx].isUsed=true;
              }else{
                this.showPosition(d.data.message);
              }
            }, (d) => {
              this.showPosition('网络繁忙！请稍候重再试');
      });
    },
    onHide () {
    },
    onShow () {
    },
    showPosition (t) {
      this.showPositionValue = true
      this.tips=t;
    },
    describe(i) {
      this.show=true;
      this.idx=i;
    },
    addDate(date, days) {
            if (days == undefined || days == '') {
                days = 1;
            }
            var date = new Date(date);
            console.log(date)
            date.setDate(date.getDate() + days);
            var month = date.getMonth() + 1;
            var day = date.getDate();
            return date.getFullYear() + '-' + this.getFormatDate(month) + '-' + this.getFormatDate(day);
     },
     getFormatDate(arg) {
            if (arg == undefined || arg == '') {
                return '';
            }

            var re = arg + '';
            if (re.length < 2) {
                re = '0' + re;
            }

            return re;
      },
    createDate(){
      var arr_week = new Array("星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六");
      //var dateee = this.interviewDate.toJSON().replace(/T/g,' ').replace(/\.[\d]{3}Z/,'').replace(/-/g,'\/');
     // console.log(dateee)
      //var _obj=new Date(dateee)// +8*3600*1000
      var d=new Date();
      for(var i=1;i<5;i++){
        //var _time=this.addDate(_obj,1);
       // _obj=new Date(_time)
          //var _m=_obj.getMonth()+1;
           d.setDate(d.getDate()+1);
          var _date=d.getDate();
          var _m=d.getMonth()+1;
          var _day=arr_week[d.getDay()];
          var _time=d.getFullYear()+"/"+_m+"/"+_date;
          var _subobj={
            date:_m+"/"+_date,
            day:_day,
            time:_time
          }
         this.headerDate.push(_subobj)
      }
    },
    changeDate(index){

      this.getDescribeTime(this.headerDate[index].time)
      this.headerIdx=index;
    },
    getDescribeTime(t){
      // 显示
      this.$vux.loading.show({
      text: '加载中...'
      })
      this.$http({
          method:'POST',
          url:this.$store.state.domain+'api/XiechengJobAPI/GetInterviewTimeStatus',
          body:{"applyId": this.$route.params.id,"interviewDate": t},
          headers: {"Authorization": this.$store.state.jwt.token}
          })
      .then((d) => {
              if(d.data.success){
                this.items=d.data.content.times
                if(this.items.length==0){
                  this.selectDay="当天没有开放时间";//当天日期
                }else{
                  this.selectDay=t;//当天日期
                }
              }else{
                this.showPosition(d.data.message);
              }
              this.$vux.loading.hide();
            }, (d) => {
              this.showPosition('网络繁忙！请稍候重再试');
      });
    },
    checkTimes(t,tempIndex){
      var _h=this.addDate(t,tempIndex+1)
      this.$http({
          method:'POST',
          url:this.$store.state.domain+'api/XiechengJobAPI/GetInterviewTimeStatus',
          body:{"applyId": this.$route.params.id,"interviewDate": _h},
          headers: {"Authorization": this.$store.state.jwt.token}
          })
      .then((d) => {
              if(d.data.success){
                this.headerHasData.push(d.data.content.times.length);
                if(this.hasDataIndex==-1 && d.data.content.times.length>0){
                  this.hasDataIndex=tempIndex;
                  this.headerIdx=tempIndex;
                  this.items=d.data.content.times;
                  this.selectDay=this.headerDate[tempIndex].time;//当天日期
                  //this.getDescribeTime(this.addDate(t,this.hasDataIndex))// 获取预约时间段
                }
                if(tempIndex<3){
                  this.checkTimes(t,tempIndex+1);
                }else{
                  if(this.selectDay==''){
                    this.selectDay="当天没有开放时间";
                  }
                  this.$vux.loading.hide()
                }
              }
            }, (d) => {
              this.showPosition('网络繁忙！请稍候重再试');
      });
    }
  }
}



